.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    background-color:white;
    margin: 10px 10px 10px 10px;
    width: 60%;
    padding: 5px;
    z-index: 1;
}

.product__info {
    height: 100px;
    margin-bottom: 15px;
}

.product__rating {
    display: flex;
}

.product__Image {
    height: 100%;
    width: 50%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product__Button {
    background-color: #f0c14b;
    border: 1px solid;
    border-color: bisque;
    margin-bottom: 15px;
}