.checkoutProduct {
    display: flex;
}

.checkoutProduct__image {
    height: 180px;
    width: 180px;
    object-fit: contain;
    margin-bottom: 15px;
}

.checkoutProduct__title {
    font-size: 20px;
    font-weight: 800;
}

.checkoutProduct__info {
    padding-left: 20px;
}

.checkoutProduct__info > button {
    background-color: #f0c14b;
    border: 1px solid;
    border-color: bisque;
    margin-top: 10px;
    margin-bottom: 15px;
    color: #111;
}

