.header {
    background-color: #131921;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
}

.Header__logo {
    width: 70px;
    object-fit: contain;
    margin-top: 18px;
    margin-left: 20px;
    margin-right: 20PX;
    margin-bottom: 18px;
}

.Header__search {
    display: flex;
    flex: 1;
}

.Header__searchBar {
    height: 20px;
    padding: 10px;
    width: 100%;
    border: none;
}

.header__option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}

.header__optionLineOne {
    font-size: 10px;
}

.header__optionLineTwo {
    font-size: 13px;
    font-weight: 800;
}

.header__optionBasket {
    display: flex;
    align-items: center;
}

.header__optionCount {
    margin-left: 10px;
    margin-right: 10px;
}

.Header__searchIcon {
    background-color: #cd9042;
    padding: 10px;
    height: 20px !important;
}

.Header__nav {
    display: flex;
    justify-content: space-evenly;    
}

.Header__link {
    text-decoration: none;
    color: white;
}